// https://www.omdbapi.com/?apikey=cc6f63d4&t=[title]

const API_URL = 'https://www.omdbapi.com/?apikey=cc6f63d4&';

export const getMovie = (title: string) => {
  return fetch(`${API_URL}t=${title}`)
    .then(result => {
      if (!result.ok) {
        throw new Error(`${result.status} - ${result.statusText}`);
      }

      return result.json();
    });
};
